body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-image: url(/API-Unsplash-Redux/static/media/bg.0534ae1c.jpg);
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  height: auto;
}

.App-search {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 80vh;
}

.App-search-content {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: start;
          align-items: start;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 50%;
}

.App-search-content input {
  width: 100%;
  height: 50px;
  padding: 0 20px;
}

.App-search-content small {
  text-align: left;
}

.App-images-content {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 100%;
  height: auto;
  padding: 50px 0;
  background-color: #e0e0e0;
}

.App-images-search {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 50px 0;
}

.col-images {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-width: 33.33%;
}

.col-images a {
  padding: 0 10px;
}


@media (max-width: 650px) {
  .App-search-content {
    -webkit-align-items: center;
            align-items: center;
    width: 80%;
    margin: 0 10%;
  }

  .App-search-content input {
    width: 100%;
  }

  .App-search {
    width: 80%;
    padding: 0 10%;
  }

  .App-images-search .col-images a {
    margin: 15px 0;
  }

  .App-images-search .col-images img {
    width: 90%;
  }

  .col-images a {
    padding: 0;
  }
}
